$post-offset-spacing-xl: 7.5rem;
$post-content-width-lg: 80%;

.post {
  .post-title {
    &.h3-font-size {
      font-size: $h3-font-size-sm;
      font-weight: bold;

      @include media-breakpoint-up(lg) {
        font-size: $h3-font-size-lg;
      }

      @include media-breakpoint-up(xl) {
        font-size: $h3-font-size;
      }
    }
  }

  main {
    h2 {
      &#fazit,
      &#conclusion {
        background-color: var(--color-green-blue);
        color: var(--color-white);
        padding: $spacing-content-default $spacing-content-default 2.5rem;
        margin: 0;

        @include media-breakpoint-up(lg) {
          width: $post-content-width-lg;
        }

        ~ p {
          background-color: var(--color-green-blue);
          color: var(--color-white);
          padding: 0 $spacing-content-default $spacing-content-default;
          margin-top: -1px;
          margin-bottom: 0;
        }
      }
    }

    h6:last-of-type {
      margin-top: $spacing-content-default;
      margin-bottom: 0;
      padding-top: spacing(14);
      font-size: $font-size-4;

      + ul {
        margin-bottom: $spacing-content-default;
        list-style: none;
        padding-top: $spacing-content-default;
        padding-bottom: spacing(8);

        li {
          padding-bottom: spacing(4);
          width: calc(100% - #{spacing(4)});

          @include media-breakpoint-up(md) {
            width: 75%;
          }

          @include media-breakpoint-up(xl) {
            width: 60%;
          }
        }

        a {
          &::before {
            content: '';
            display: inline-block;
            background-image: url('../svg/arrow-right.svg');
            background-repeat: no-repeat;
            width: 28px;
            height: 17px;
            float: right;
            transform: translateX(spacing(4)) translateY(spacing(1)) rotate(-45deg);
          }

          &:hover {
            &::before {
              background-image: url('../svg/arrow-right-blue-medium.svg');
            }
          }
        }
      }
    }

    h6:last-of-type + ul,
    h6 {
      &:last-of-type {
        background-color: var(--color-black-4);
        width: 100%;
        padding-left: spacing(8);
        padding-right: spacing(8);

        @include media-breakpoint-up(xl) {
          width: calc(100% + (2 * #{$post-offset-spacing-xl}));
          margin-left: -#{$post-offset-spacing-xl};
          padding-left: $post-offset-spacing-xl;
          padding-right: $post-offset-spacing-xl;
        }
      }
    }

    h2,
    h3 {
      margin: 2rem 0;
    }
  }

  blockquote {
    border-left: 6px solid var(--color-green-blue);
    padding-left: 1.5em;
  }
}

.post-content {
  main {
    > blockquote,
    > p {
      &:last-of-type {
        margin-bottom: 0;

        img {
          margin-bottom: 0;
        }
      }

      &:first-of-type {
        img {
          margin-top: 0;
        }
      }

      @include media-breakpoint-up(lg) {
        width: $post-content-width-lg;
      }
    }

    > p {
      &:not(.post__sticky-excerpt) {
        line-height: 2;
      }
    }
  }

  iframe {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
  }

  > img {
    &:first-of-type {
      margin-top: 0;

      + figcaption {
        margin-top: -2rem;
        margin-bottom: 2.5rem;

        @include media-breakpoint-up(lg) {
          position: relative;
          margin-top: -3rem;
          margin-bottom: 3.5rem;
        }

        @include media-breakpoint-up(xl) {
          left: -#{$post-offset-spacing-xl};
        }
      }
    }
  }

  img {
    border-radius: 0;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
  }

  @include media-breakpoint-up(lg) {
    img {
      margin-top: 3.5rem;
      margin-bottom: 3.5rem;
      left: 0;
      position: relative;
    }

    iframe {
      margin-top: 2rem;
      margin-bottom: 2rem;
    }
  }

  @include media-breakpoint-up(xl) {
    img {
      left: -#{$post-offset-spacing-xl};
      max-width: get-container-width(
        $name: 'xl',
        $with-padding: true,
        $right-offset-width: $post-offset-spacing-xl / 2
      );
    }
  }
}

.post__sticky-bar {
  margin-bottom: 1rem;

  @include media-breakpoint-up(xl) {
    display: flex;
    margin-bottom: 0;
    margin-left: -#{$post-offset-spacing-xl};
  }
}

.post__sticky-socials {
  @include media-breakpoint-up(xl) {
    height: 10px !important; /* force the sticky plugin to not create a gap */
    flex-shrink: 0;
    flex-basis: $post-offset-spacing-xl;
  }
}
