.#{$site} {
  @include media-breakpoint-up(lg) {
    .navbar-expand-lg {
      .mega-menu-body {
        padding-left: 0;
      }

      *[class^="col-"] {
        padding-left: 0;
      }
    }

    .navbar-nav {
      .nav-link {
        padding-right: $header-nav-link-padding-x;
        padding-left: $header-nav-link-padding-x;
      }

      .dropdown-menu {
        border-top: 0;
        margin-top: 0;
      }
    }

    .navbar-nav-group {
      flex: 0 0 240px;
    }
  }
}
