$author-scale: 1.6;
$author-percent: calc(100% * #{$author-scale});

.author {
  @include media-breakpoint-up(lg) {
    margin-left: $bootstrap-space-3;
    width: calc(100% - #{$bootstrap-space-3});
  }

  @include media-breakpoint-up(xl) {
    margin-left: $bootstrap-space-4;
    width: calc(100% - #{$bootstrap-space-4});
  }
}

.author__avatar-frame {
  @include aspect-ratio(1, 1);

  overflow: hidden;
  border-radius: 100%;
  box-shadow: 0 0 0 1px rgba($gray-600, 0.1);

  > * {
    width: 100%;
    height: 100%;
  }

  img {
    width: $author-percent;
    max-width: $author-percent;
    transform: translateX(-50%) translateY(-50%);
  }
}

.author__intro {
  font-weight: 300;
}

.author__posts-list {
  border-top: 2px solid var(--color-trout);

  .row {
    margin-left: -#{$bootstrap-row-spacing};
    margin-right: -#{$bootstrap-row-spacing};
  }
}

.author__posts {
  text-transform: uppercase;
  font-size: 0.75rem;
  color: var(--color-trout);
  font-weight: bold;
}
