@mixin link-hover {
  position: relative;
  display: inline-block;

  &:hover,
  &:active,
  &:focus {
    font-weight: bold;
  }

  &::after {
    display: block;
    content: attr(data-title);
    font-weight: bold;
    height: 0;
    width: auto;
    overflow: hidden;
    visibility: hidden;
    margin-left: 0;
    transition: none;
  }
}

@mixin link-active {
  position: relative;

  &.active {
    color: inherit;

    &::before {
      background-color: var(--color-yellow);
      content: '';
      height: 8px;
      left: 50%;
      position: absolute;
      top: 57%;
      transform: translateX(-50%);
      width: calc(100% - (#{$header-nav-link-padding-x} * 2) + 8px);
      z-index: -1;
    }
  }
}

@mixin aspect-ratio($width, $height) {
  position: relative;

  &:before {
    content: '';
    display: block;
    width: 100%;
    padding-top: ($height / $width) * 100%;
  }

  > * {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

@function get-container-width($name, $with-padding: true, $right-offset-width: false) {
  $width: map-get($container-widths, $name);

  @if ($with-padding) {
    $padding: $header-floating-inner-padding-x;

    @if $right-offset-width {
      $padding: calc(#{$header-floating-inner-padding-x} + #{$right-offset-width});
    }

    @return calc(#{$width} - (2 * #{$padding}));
  } @else {
    @return $width;
  }
}

@mixin safari {
  @media not all and (min-resolution: 0.001dpcm) {
    @supports (-webkit-appearance: none) {
      @content;
    }
  }
}

@function spacing($size, $negative: false) {
  $value: $size * 0.25;

  $prefix: '';

  @if $negative {
    $prefix: '-';
  }

  @return #{$prefix}#{$value}rem;
}

@function fa-content($fa-var) {
  // prettier-ignore
  @return unquote("\"#{ $fa-var }\"");
}

@mixin icon($name, $font: '', $weight: 300, $after: true) {
  $selector: 'before';

  @if ($after) {
    $selector: 'after';
  }

  $icon: map-get($icons, $name);

  $icon-font: $icon-font-free;

  @if ($font == 'brand') {
    $icon-font: $icon-font-brand;
  }

  &::#{$selector} {
    font-family: $icon-font;
    font-style: normal;
    font-variant: normal;
    font-weight: $weight;
    -webkit-font-smoothing: antialiased;
    line-height: 1;
    text-rendering: auto;
    content: fa-content($icon);

    @content;
  }
}
