.#{$site} {
  .header {
    &.is-hovering.is-open,
    &.is-open {
      &.header-floating-lg {
        background-color: var(--color-white);
        box-shadow: $header-section-box-shadow;
      }
    }

    &.is-hovering {
      transition: background-color .8s $animation-transition;

      &:not(.js-header-fix-moment):not(.is-open) {
        &.header-floating-lg {
          background-color: rgba(#fff, .6);
          box-shadow: $header-section-box-shadow;
        }
      }
    }

    &.text-white {
      @include media-breakpoint-up(lg) {
        .nav-link {
          color: $header-nav-link-color;

          &:hover,
          &.active {
            color: $header-nav-link-color;
          }
        }

        &.is-open {
          .nav-link {
            color: $header-nav-link-color;

            &:hover,
            &.active {
              color: $header-nav-link-color;
            }
          }
        }

        &:not(.is-hovering):not(.is-open):not(.js-header-fix-moment) {
          .dropdown-toggle {
            &::after {
              filter: brightness(0) invert(1);
            }
          }

          .header__logo--light {
            display: block;
          }

          .header__logo {
            display: none;
          }

          .nav-link {
            color: $header-nav-link-color-light;

            &.text-black-50,
            .text-black-50 {
              color: $header-nav-link-color-light !important;
            }
          }
        }
      }
    }

    .nav-link {
      color: $header-nav-link-color;
    }

    @include media-breakpoint-up(lg) {
      .hs-mega-menu {
        box-shadow: none;

        &::before {
          content: '';
          border-top: 2px solid rgba(#7f7f7f, .1);
          position: absolute;
          width: 200vw;
          height: 100%;
          left: -100%;
          background-color: var(--color-white);
          box-shadow: $header-section-box-shadow;
        }
      }

      .nav-link {
        @include link-hover;
        @include link-active;

        &.hs-unfold {
          &::after {
            margin-right: 1.5rem;
          }
        }
      }
    }

    @include media-breakpoint-up(xl) {
      .hs-mega-menu {
        > .row {
          margin-left: calc(#{$header-navbar-brand-width} + #{$header-logo-spacing-right} + #{$header-nav-link-padding-x});
        }
      }
    }
  }

  .navbar-brand {
    &,
    & > img {
      width: 8rem;

      @include media-breakpoint-up(lg) {
        width: $header-navbar-brand-width;
      }
    }
  }

  .navbar-promo-icon {
    &.streamline-icon {
      max-width: 2.75rem;
      min-width: 2rem;
    }
  }

  .navbar-nav-group {
    .dropdown-item {
      &:hover {
        color: var(--color-blue-medium);
      }
    }
  }

  .navbar-promo-card-deck {
    .navbar-promo-item {
      border-width: 0;
      margin-left: 0;
      margin-right: 0;

      .navbar-promo-link {
        &::after {
          display: none;
        }
      }
    }

    @include media-breakpoint-up(md) {
      .navbar-promo-item {
        &:first-of-type {
          margin-left: 0;
        }

        &:last-of-type {
          margin-right: 0;
        }
      }

      .navbar-promo-link {
        &:hover {
          .navbar-promo-title {
            color: var(--color-blue-medium);
          }
        }
      }
    }

    @include media-breakpoint-up(lg) {
      .navbar-promo-item {
        &:first-of-type {
          margin-right: 1.5rem;
        }
      }
    }
  }

  .navbar-banner {
    border-bottom-left-radius: 0;

    &::after {
      border-bottom-left-radius: 0;
    }
  }

  .navbar-child { /* force the mobile navigation to have the dropdown look */
    .dropdown-item-toggle {
      color: var(--color-black);

      &.d-block {
        display: flex !important;
      }
    }

    .dropdown-menu {
      .dropdown-item {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
      }
    }

    @include media-breakpoint-up(lg) {
      .dropdown-item-toggle {
        color: var(--color-green-blue);
        pointer-events: none;
        font-size: 1rem;
        font-weight: bold;

        &::after {
          display: none;
        }
      }

      .dropdown-menu {
        margin-bottom: 0 !important;
        display: block !important;
        position: initial;
        box-shadow: none;
        border-radius: 0;

        .dropdown-item {
          padding-left: 0;
          padding-right: 0;
        }
      }
    }
  }
}


.header__logo--light {
  display: none;
}
