.#{$site} {
  .hero-text-image {
    h1 {
      &.h2-font-size {
        font-size: $h2-font-size-sm;
        font-weight: bold;

        @include media-breakpoint-up(lg) {
          font-size: $h2-font-size-lg;
        }

        @include media-breakpoint-up(xl) {
          font-size: $h2-font-size;
        }
      }
    }
  }

  .container {
    &.hero-text-image__background {
      padding-right: 0;

      .row {
        > div {
          padding-right: 0;
        }
      }
    }
  }
}


.hero-text-image {
  &.has-content-img {
    .hero-text-imge__img-spacer {
      @include media-breakpoint-up(lg) {
        display: none;

        + .hero-text-image__wrapper {
          position: relative;
        }
      }
    }
  }

  &.is-fullscreen {
    overflow: hidden;

    .hero-text-image__background {
      top: auto;
      bottom: -4rem;
    }

    .hero-text-image__fullscreen {
      width: 100%;
      height: 100%;
      text-align: center;

      img {
        object-fit: cover;
        min-height: 100%;
      }
    }
  }
}


.hero-text-image__overline {
  @include background-grow(var(--color-yellow));
}


.hero-text-image__background {
  &.position-absolute {
    @include media-breakpoint-down(md) {
      position: relative !important;
    }
  }
}


.hero-text-imge__img-spacer {
  display: none;

  @include media-breakpoint-up(lg) {
    display: block;
    opacity: 0;
    pointer-events: none;

    + .hero-text-image__wrapper {
      position: absolute;
      left: 0;
      top: 0;
    }
  }
}


.hero-text-image__wrapper {
  position: relative;
}


.hero-text-image__keyvisual {
  max-height: 600px;

  &.is-large {
    @include media-breakpoint-up(lg) {
      max-height: 900px;
    }
  }

  @include media-breakpoint-up(lg) {
    max-height: 700px;
  }
}
