.logo-list__item {
  box-shadow: 0 0 16px rgba(0, 0, 0, .22);
  flex-basis: 100%;
  filter: grayscale(1);
  opacity: .4;
  transition-property: filter, opacity, box-shadow;
  transition-duration: .6s;
  transition-timing-function: $animation-transition;

  img {
    object-fit: contain;
    height: inherit;
    height: intrinsic;
  }

  &:hover {
    filter: grayscale(0);
    opacity: 1;
    box-shadow: 0 0 16px rgba(0, 0, 0, .15);
  }

  @include media-breakpoint-up(md) {
    box-shadow: none;
    flex-basis: 48%;
    max-width: 48%;
  }

  @include media-breakpoint-up(lg) {
    flex-basis: 24%;
    max-width: 24%;
  }
}
