.#{$site} {
  .header-section {
    background-color: transparent;
  }

  .header-floating-lg {
    margin-top: 0;

    &:not(.js-header-fix-moment) {
      background-color: transparent;
      box-shadow: none;
    }

    &.js-header-fix-moment {
      box-shadow: $header-section-box-shadow;
    }
  }

  .header-box-shadow {
    .header-section {
      box-shadow: none;
    }
  }

  .header-meta {
    .nav-link {
      &.active {
        font-weight: bold;

        &::before,
        &::after {
          display: none;
        }
      }

      &:last-of-type {
        padding-right: 0;
      }
    }
  }

  @include media-breakpoint-up(lg) {
    .header-meta,
    .header-floating-inner {
      padding-left: $header-floating-inner-padding-x;
      padding-right: $header-floating-inner-padding-x;
    }
  }
}
