.cta {
  position: relative;
  border-radius: 0;

  &.link {
    padding-right: 2.25rem;
    margin-right: 0.5rem;
    font-size: inherit;

    &.is-light {
      &:hover::after,
      &::after {
        background-image: url('../svg/arrow-right.svg');
        filter: brightness(400%);
      }
    }

    &::after {
      @include safari {
        top: 6px;
      }

      position: absolute;
      content: '';
      background-image: url('../svg/arrow-right.svg');
      background-repeat: no-repeat;
      right: 0;
      width: 28px;
      height: 17px;
    }

    &:hover {
      &::after {
        animation: moveRightLeft;
        animation-duration: .4s;
        animation-fill-mode: forwards;
        animation-timing-function: $animation-transition;
        background-image: url('../svg/arrow-right-blue-medium.svg');
      }
    }
  }
}

.#{$site} {
  .cta {
    color: var(--color-blue-light);

    &:not(.btn):not(.custom):not(.nav-link) {
      &.is-light {
        color: var(--color-white);

        &:hover {
          color: var(--color-white);
        }
      }
    }

    &.btn-primary {
      background-color: var(--color-blue-light);
      color: var(--color-white);
      border: 0;

      &::after {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        content: attr(data-text);
        color: var(--color-white);
        display: flex;
        justify-content: center;
        align-items: center;
        background: linear-gradient(to right, var(--color-blue-medium) 50%, transparent 50%);
        background-size: 200% 100%;
        background-position: right bottom;
        opacity: 0;
        transition: background .5s $animation-transition;
      }

      &.is-light {
        color: var(--color-blue-light);
        background-color: var(--color-white);

        &::after {
          display: none;
        }

        &:hover {
          color: var(--color-blue-medium);
        }
      }

      &:hover {
        &::after {
          opacity: 1;
          background-position: left bottom;
        }
      }
    }

    &.btn-secondary {
      background-color: transparent;
      border-color: var(--color-blue-light);
      color: var(--color-blue-light);

      &.is-light {
        border-color: var(--color-white);
        color: var(--color-white);

        &::after {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          content: attr(data-text);
          color: var(--color-blue-medium);
          display: flex;
          justify-content: center;
          align-items: center;
          background: linear-gradient(to right, rgba(255, 255, 255, .8) 50%, transparent 50%);
          background-size: 200% 100%;
          background-position: right bottom;
          opacity: 0;
          transition: background .5s $animation-transition;
        }

        &:hover {
          border-color: var(--color-white);
          color: var(--color-blue-medium);

          &::after {
            opacity: 1;
            background-position: left bottom;
          }
        }
      }

      &:hover {
        border-color: var(--color-blue-medium);
        color: var(--color-blue-medium);
      }
    }

    &:hover {
      box-shadow: none;
      color: var(--color-blue-medium);
    }
  }
}
