q { quotes: "„" "“" "‚" "‘" }

.quote {
  color: var(--color-black-4);
  font-weight: 300;
  line-height: 1.3;

  &::before {
    position: absolute;
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background-color: var(--color-green-blue);
  }
}


.quote__start {
  &::after {
    position: relative;
    font-size: 15rem;
    line-height: 0;
    content: '„';
    color: var(--color-yellow);
    left: 0;
    top: -60%;
    pointer-events: none;
  }
}
