$animation-transition: cubic-bezier(.19, 1, .20, 1);

@mixin background-grow($color) {
  position: relative;
  z-index: 1;

  &::before {
    position: absolute;
    content: '';
    display: block;
    height: 100%;
    width: 0;
    left: 0;
    top: 0;
    background-color: $color;
    animation: grow;
    animation-duration: .8s;
    animation-fill-mode: forwards;
    animation-timing-function: $animation-transition;
    z-index: -1;
  }
}


@keyframes grow {
  from {
    width: 0;
  }

  to {
    width: 85%;
  }
}


@keyframes slideInUp {
  from {
    opacity: 0;
    visibility: visible;
  }

  to {
    opacity: 1;
  }
}


@keyframes moveRightLeft {
  0% {
    transform: translateX(0%);
  }

  30% {
    transform: translateX(20%);
  }

  100% {
    transform: translateX(0%);
  }
}
